:root {
  --font-main: "PT Mono", "Lucida Console", "Monaco", monospace;
  --font-secondary: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
  --font-size: 16px;
  --c-text-primary: hsla(0 0% 40% / 1);
  --c-text-secondary: hsla(0 0% 100% / 1);
  --bg-dark: hsla(201 34% 16% / 1);
  --bg-light: hsla(198 100% 50% / 1);
  --bg-hero: var(--bg-dark);
  --c-logo-description: hsla(273 10% 52% / 1); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  font-size: 0;
  vertical-align: middle; }

.grid {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none; }

.grid-overlay {
  position: relative;
  height: 100%;
  max-width: 1170px;
  margin: 0 auto; }

.grid-overlay::before {
  --grid-indent: 0;
  --grid-gap: 30px;
  --columns-count: 12;
  --columns-color: rgba(255, 0, 0, 0.1);
  --columns-width: calc(100% / var(--columns-count) - var(--grid-gap));
  content: "";
  pointer-events: none;
  position: absolute;
  z-index: 999;
  top: 0;
  left: var(--grid-indent);
  right: var(--grid-indent);
  bottom: 0;
  background-image: repeating-linear-gradient(to right, var(--columns-color), var(--columns-color) var(--columns-width), transparent var(--columns-width), transparent calc(var(--columns-width) + var(--grid-gap)));
  background-size: calc(100% + var(--grid-gap)) 100%; }
  @media (max-width: 1023px) {
    .grid-overlay::before {
      --grid-gap: 25px; } }
  @media (max-width: 769px) {
    .grid-overlay::before {
      --columns-count: 6;
      --grid-gap: 15px; } }

.page__body {
  margin: 0;
  font-family: var(--font-main);
  font-size: var(--font-size); }

.page__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px; }
  @media (min-width: 1024px) {
    .page__container--header {
      max-width: 1441px; } }
  @media (max-width: 1023px) {
    .page__container {
      padding: 0 25px; } }
  @media (max-width: 767px) {
    .page__container {
      padding: 0 15px; } }

.hero {
  padding-top: 33px;
  background-color: var(--bg-hero); }
  @media (max-width: 1023px) {
    .hero {
      padding-top: 20px; } }
  @media (max-width: 767px) {
    .hero {
      padding-top: 17px; } }

.header__content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__logo {
  -ms-flex-item-align: start;
      align-self: start; }

.header__callback {
  -ms-flex-item-align: start;
      align-self: start;
  margin-top: 4px; }
  @media (max-width: 1023px) {
    .header__callback {
      display: none; } }

.logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }
  @media (max-width: 767px) {
    .logo--header .logo__text {
      display: none; }
    .logo--header img {
      width: 38px;
      height: 38px; } }

.logo__text {
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin-left: 16px; }
  @media (max-width: 767px) {
    .logo__text {
      display: none; } }

.logo__name {
  display: block;
  font-family: var(--font-secondary);
  font-size: 25px;
  font-weight: 700;
  color: var(--c-text-secondary); }

.logo__description {
  font-family: "Roboto", "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: var(--c-logo-description); }

.contacts {
  display: grid;
  grid-template-columns: auto auto;
  -webkit-column-gap: 90px;
     -moz-column-gap: 90px;
          column-gap: 90px;
  row-gap: 15px;
  margin-left: auto;
  margin-right: 48px; }
  @media (max-width: 1023px) {
    .contacts {
      margin-right: 0;
      -webkit-column-gap: 33px;
         -moz-column-gap: 33px;
              column-gap: 33px; } }
  @media (max-width: 767px) {
    .contacts {
      -webkit-column-gap: 23px;
         -moz-column-gap: 23px;
              column-gap: 23px; } }

.contacts__link {
  font-family: inherit;
  color: var(--c-text-secondary);
  text-decoration: none;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex; }
  @media (max-width: 1023px) {
    .contacts__link--email svg {
      width: 26px;
      height: 19px; }
    .contacts__link--phone svg {
      width: 24px;
      height: 24px; } }

.contacts__link span {
  margin-left: 16px; }
  @media (max-width: 1023px) {
    .contacts__link span {
      display: none; } }

.button {
  padding: 13px 27px;
  border: 1px solid currentColor;
  border-radius: 5px;
  font-family: var(--font-secondary);
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--c-text-secondary);
  cursor: pointer; }
